.bookmark-content {
  text-align: center;
  margin-top: 22vh;
  color: white;
}
.bookmark-content .hd1 {
    font-size: 100px;
    font-weight: 200;
}
.bookmark-content .desc1 {
    font-size: 18px;
    margin-top: 22vh;
    font-weight: 400;
}
.bookmark-content .down-arrow {
    width: 40px;
    margin-top: 40px;
}
.bookmark-section2 {
  background: white;
  padding: 50px 20px;
  position: absolute;
  width: 100%;
  left: 0;
  height: 40%;
  margin-top: 11vh;
  text-align: center;
}
.bookmark-section2 .hd1 {
    font-size: 22px;
    color: #313131;
    font-weight: 300;
    line-height: 2.5;
    text-align: center;
}
.bookmark-section2 .black-btn {
    background: white;
    border: solid 1px #313131 !important;
    padding: 12px 20px;
    font-size: 22px;
    color: #313131;
    font-weight: 300;
    margin-top: 50px;
    border-radius: 3px;
    cursor: pointer;
    outline: none;
    min-width: 40%;
    border: 0;
    -webkit-transition: 0.1s all linear;
    transition: 0.1s all linear;
}
.bookmark-section2 .black-btn:hover {
      background: #313131;
      color: #ffffff;
}
.bookmark-section2 .black-btn:active {
      -webkit-box-shadow: inset 0px 0px 2px 0px #787878;
              box-shadow: inset 0px 0px 2px 0px #787878;
}
